import { BigNumber } from 'ethers';
import { useEffect, useState } from 'react';
import styled from 'styled-components';

import Button from '../../../components/Button';
import InfoTip from '../../../components/InfoTip';
import Input from '../../../components/Input';
import BuySellOfferModal from '../modals/BuySellOfferModal';

interface IProps {
  baseTokenBalance: BigNumber,
  action: string,
  selectQuoteToken: {
    name: string,
    balance: any
  }
}

export default function BuySellTable(props: IProps) {

  const { action, selectQuoteToken, baseTokenBalance } = props;

  const [quoteTokenInput, setQuoteToken] = useState<string>('')
  const [baseTokenInput, setBaseToken] = useState<string>('')

  const [buyTotal, setBuyTotal] = useState<string>('0')
  const [sellTotal, setSellTotal] = useState<string>('0')
  const [openOfferModal, setOpenOfferModal] = useState<boolean>(false)

  const hasEnoughBalance = action === "Buy" ? (
    // Number(selectQuoteToken.balance) >= Number(quoteTokenInput) &&
    Number(selectQuoteToken.balance) >= Number(buyTotal)
  ) : (
    Number(baseTokenBalance) >= Number(baseTokenInput)
    // &&
    // Number(baseTokenBalance) >= Number(sellTotal)
  )



  const inputToken = action === "Buy" ? selectQuoteToken.name : 'ARTH-DP'
  const hasValidPrice = action === "Buy" ? Number(buyTotal) > 0 : Number(sellTotal) > 0
  const enableActionButton = hasEnoughBalance && hasValidPrice

  console.log('fuc', Number(baseTokenBalance), Number(baseTokenInput))

  useEffect(() => {
    if (quoteTokenInput.length && baseTokenInput.length) {
      if (action === 'Buy') setBuyTotal((Number(quoteTokenInput) * Number(baseTokenInput)).toString())
      if (action === 'Sell') setSellTotal((Number(quoteTokenInput) * Number(baseTokenInput)).toString())
    } else {
      setBuyTotal('0')
      setSellTotal('0')
    }
  }, [quoteTokenInput, action, baseTokenInput])

  return (
    <CardContent>
      <CardSection>
        <CardColumn1
          className={'table-border single-line-center-center'}>Price</CardColumn1>
        <CardColumn2
          className={'table-border text-right'} >
          <Input
            value={quoteTokenInput}
            setValue={setQuoteToken}
            alignInput={'right'}
          />
        </CardColumn2>
        <CardColumn3
          className={'table-border single-line-center-center'}>{selectQuoteToken.name}/ARTH-DP</CardColumn3>
      </CardSection>
      <CardSection>
        <CardColumn1 className={'table-border single-line-center-center'}>Amount</CardColumn1>
        <CardColumn2 className={'table-border text-right'}>
          <Input
            value={baseTokenInput}
            setValue={setBaseToken}
            alignInput={'right'}
          />
        </CardColumn2>
        <CardColumn3 className={'table-border single-line-center-center'}>ARTH-DP</CardColumn3>
      </CardSection>
      <CardSection style={{ marginBottom: '20px', background: '#222' }}>
        <CardColumn1 className={'table-border single-line-center-center'}>Total</CardColumn1>
        <CardColumn2 className={'table-border text-right'}>
          <div style={{ padding: 10, background: '#222' }}>
            <>
              {action === 'Buy' ? buyTotal.toString() : sellTotal.toString()}
              {action === 'Buy' ? setBuyTotal : setSellTotal}
            </>
          </div>
          {/* <Input
            disabled={true}
            value={action === 'Buy' ? buyTotal.toString() : sellTotal.toString()}
            setValue={action === 'Buy' ? setBuyTotal : setSellTotal}
            alignInput={'right'}
          /> */}
        </CardColumn2>
        <CardColumn3 className={'table-border single-line-center-center'}>{selectQuoteToken.name}</CardColumn3>
      </CardSection>
      <CardSection style={{ marginBottom: '20px', alignItems: 'center', }}>
        {
          !hasValidPrice ?
            <InfoTip type={'Warning'} msg={'Please enter a price and amount.'} /> :
            (
              !hasEnoughBalance &&
              <InfoTip type={'Error'} msg={`You don't have enough ${inputToken} tokens`} />
            )
        }
      </CardSection>
      <CardSection style={{ alignItems: 'center' }}>
        <Button
          disabled={!enableActionButton}
          text={`${action} Debt`}
          onClick={() => setOpenOfferModal(true)}
        />
      </CardSection>
      <BuySellOfferModal
        openModal={openOfferModal}
        onModalClose={() => setOpenOfferModal(false)}
        action={action}
        tableData={{
          quote: quoteTokenInput,
          base: baseTokenInput,
          total: action === 'Buy' ? buyTotal.toString() : sellTotal.toString(),
          quoteTokenBalance: selectQuoteToken.balance,
          selectQuoteToken
        }}
      />
    </CardContent>
  )
}


const CardContent = styled.div`
  display: flex;
  padding: 0 32px 32px 32px;
  align-items: self-start;
  margin-top: 24px;
  flex-direction: column;
  @media (max-width: 600px) {
    padding: 0 16px 16px 16px;
  }

`;

const CardSection = styled.div`
  display: flex;
  flex-direction: row;
  width: 100%;
  &:last-child {
    margin-bottom: 0;
  }
  &.right {
    text-align: right;
  }
`;

const CardColumn1 = styled.div`
  flex-basis: 20%;
`
const CardColumn2 = styled.div`
  flex-basis: 40%;
`
const CardColumn3 = styled.div`
  flex-basis: 40%;
`
